import { AgGridVue } from 'ag-grid-vue3';
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  components: {
    AgGridVue,
    Swal
  },
  data() {
    return {
      rowData: [{
        company_code: "",
        company_name: "",
        address: "",
        tel: "",
        emp_code: "",
        company_type: ""
      }],
      columnDefs: [{
        field: 'company_code',
        headerName: '업체코드',
        flex: 3
      }, {
        field: 'company_name',
        headerName: '업체명',
        flex: 3
      }, {
        field: 'address',
        headerName: '위치',
        flex: 6
      }, {
        field: 'tel',
        headerName: '연락처',
        flex: 3
      }, {
        field: 'emp_code',
        headerName: '담당자',
        flex: 2
      }, {
        field: 'company_type',
        headerName: '업체타입',
        flex: 2,
        valueFormatter: params => {
          if (params.value == 'CT1') {
            return params.value = '입고업체';
          } else if (params.value == 'CT2') {
            return params.value = '판매업체';
          } else if (params.value == 'CT3') {
            return params.value = '설비수리업체';
          } else if (params.value == 'CT4') {
            return params.value = '설비구매업체';
          }
        }
      }],
      gridOptions: {
        pagination: true,
        paginationPageSize: 10,
        paginationPageSizeSelector: [5, 10, 20, 50],
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          //컬럼 드래그로 순서바꾸기 못하게
          resizable: false,
          //컬럼 너비 마우스로 조절 못하게
          sortable: false,
          //정렬 기능 비활성화
          onGridReady: function (event) {
            event.api.sizeColumnsToFit();
          }
        }
      },
      selectedSecondIndex: null,
      //상세그리드 행 인덱스
      prodIndex: null,
      info: {
        company_code: "",
        company_name: "",
        address: "",
        tel: "",
        emp_code: "",
        company_type: ""
      },
      companyListAry: [],
      selectedSecondIndex: null,
      prodIndex: null
    };
  },
  async mounted() {
    await this.companyData();
    await this.companyList();
  },
  methods: {
    //업체목록
    async companyData() {
      try {
        const response = await axios.get('/api/admin/company');
        this.rowData = response.data;
      } catch (err) {
        console.log('데이터 조회 실패', err);
      }
    },
    comellClicked(event) {
      let com = event.data.company_code;
      this.prodIndex = event.rowIndex;
      axios.get('/api/admin/company/' + com).then(res => {
        console.log(res);
        this.info = res.data[0];
      }).catch(error => {
        console.error(error);
      });
      console.log(event);
    },
    //업체타입 리스트
    async companyList() {
      await axios.get('/api/admin/comTypeList').then(res => {
        console.log(res.data);
        this.companyListAry = res.data;
      });
    },
    //등록
    async comSave() {
      for (let data in this.info) {
        if (data.company_name == '' || data.address == '' || data.tel == '' || data.emp_code == '' || data.company_type == '') {
          Swal.fire({
            title: '실패',
            text: '값을 다 입력해주세요',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }
      console.log(this.rowData[this.prodIndex]);
      //등록시작
      const res = await axios.post('/api/admin/comSave', {
        insertCom: this.info
      }).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '등록 성공',
            text: '정상적으로 등록이 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.info = {};
        } else {
          Swal.fire({
            title: '등록 실패',
            text: '등록이 실패하였습니다..',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }).catch(error => {
        console.error(error);
        Swal.fire({
          title: '등록 실패',
          text: '알수 없는 오류가 발생하였습니다..',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.info = {
        company_code: "",
        company_name: "",
        address: "",
        tel: "",
        emp_code: "",
        company_type: ""
      };
      await this.companyData();
      this.info = {
        ...this.info
      };
    },
    //수정
    //값 다 넣었는지 체크
    async comUpdate() {
      let validation = this.checkValue();
      if (validation == 1) {
        return;
      }
      if (this.info.emp_code == '') {
        Swal.fire({
          title: '수정 불가',
          text: '업체코드가 필요합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }

      //수정시작
      await axios.put('/api/admin/comUpdate', this.info).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '수정 완료',
            text: '수정이 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.companyData();
          this.info = {
            company_code: "",
            company_name: "",
            address: "",
            tel: "",
            emp_code: "",
            company_type: ""
          };
        } else {
          Swal.fire({
            title: '수정 실패',
            text: '수정을 실패하였습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }).catch(err => {
        console.error(err);
        Swal.fire({
          title: '수정 실패',
          text: '알수 없는 에러.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.info = [{
        company_code: "",
        company_name: "",
        address: "",
        tel: "",
        emp_code: "",
        company_type: ""
      }];
    },
    //삭제
    async comDelete() {
      if (this.info.company_code == '') {
        Swal.fire({
          title: '삭제 실패',
          text: '삭제코드가 필요합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      await axios.delete('/api/admin/comDelete/' + this.info.company_code).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '삭제 성공',
            text: '정상적으로 삭제되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.companyData();
          this.info = {
            company_code: "",
            company_name: "",
            address: "",
            tel: "",
            emp_code: "",
            company_type: ""
          };
        } else {
          Swal.fire({
            title: '삭제 실패',
            text: '삭제를 실패하였습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }).catch(err => {
        console.error(err);
        Swal.fire({
          title: '삭제 실패',
          text: '알수 없는 에러.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.info = [{
        company_code: "",
        company_name: "",
        address: "",
        tel: "",
        emp_code: "",
        company_type: ""
      }];
    },
    //초기화
    comReset() {
      this.info = [];
    }
  }
};